<script>
export default {
    name: 'course',
    data() {
        return {
          options: [{
            value: '选项1',
            label: '黄金糕'
          }, {
            value: '选项2',
            label: '双皮奶'
          }, {
            value: '选项3',
            label: '蚵仔煎'
          }, {
            value: '选项4',
            label: '龙须面'
          }, {
            value: '选项5',
            label: '北京烤鸭'
          }],
          value: '',
          editVisible:false,
          talktext:'',
          number:'',
          cartDatas:[{
            face:'',
            form:'',
            content:''
          }]
        };
    },
    methods: {
      addafter(index){//向后添加
        let data = {face:'',form:'',content:''};
        this.cartDatas.splice(index+1, 0, data)
      },
      delcarts(index){//删除卡片
        this.cartDatas.splice(index, 1)
      },
      removefront(index){//向前移动
        let data = this.cartDatas[index];
        this.cartDatas.splice(index, 1);
        this.cartDatas.splice(index-1, 0, data)
      },
      removeafter(index){//向后移动
        let data = this.cartDatas[index];
        this.cartDatas.splice(index, 1);

        this.cartDatas.splice(index+1, 0, data)
      },
      viewwork(){
        this.$router.push({path:'workdetail'});
      },
      // 保存并提交
      submitwork() {
          this.$confirm('确定提交？提交完成不能修改', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.$message.success('提交成功');
          })
          .catch(() => {});
      },
    }
};
</script>

<style lang='scss'>
