            }
            .deldiscuss{
              position:absolute;
              right:4px;
              top:4px;
              font-size:16px;
              cursor:pointer;
            }
          }
          .push-talk{
            width:62%;
            margin-left:30px;
            margin-top:30px;
            border:1px solid #ccc;
            border-radius:6px;
          }
          .push-button{
            padding-left:30px;
            margin-top:10px;
          }
        }
        .stu-list{
            border:1px solid #ccc;
        }
    }
</style>


